import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-control',
  templateUrl: './control.component.html',
  styleUrls: ['./control.component.scss']
})
export class ControlComponent implements OnInit {

  constructor(
    private router:Router
  ) { }

  ngOnInit(): void {
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/sendo'] )
  }
  gotoControlExel() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/sendo/control/control-exel']   )
  }

  gotoControlReturnControl() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/sendo/control/return-control'])
  }
  gotoControlExportGoods() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/sendo/control/export-goods'] ,{state : {dataRestore: {}}})
  }
}
